import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';


const routes: Routes = [
    {
        path: '',
        redirectTo: 'company'
    },
    {
        path: 'company',
        loadChildren: () => import('./company/company.module').then(mod => mod.CompanyModule)
    },

    {
        path: 'direktori',
        loadChildren: () => import('./direktori/direktori.module').then(mod => mod.DirektoriModule)
    },
    {
        path: 'kalender',
        loadChildren: () => import('./kalender/kalender.module').then(mod => mod.KalenderModule)
    },

    {
        path: 'payroll',
        loadChildren: () =>
            import('./payroll/payroll.module').then(mod => mod.PayrollModule),
    },

];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class HrRoutingModule { }
