import { NgModule } from '@angular/core'
import { CommonModule } from '@angular/common'

import { HrRoutingModule } from './hr-routing.module'
import { NzTableModule } from 'ng-zorro-antd'
import { ApiModule } from 'src/app/sdk/hr/api.module';
import { ApiModule as ApiModuleUser } from 'src/app/sdk/user/api.module';
import { ApiModule as ApiModulePayroll } from 'src/app/sdk/payroll/api.module';
import { ApiModule as ApiModuleDoc } from 'src/app/sdk/document/api.module';
import { HTTP_INTERCEPTORS } from '@angular/common/http';
import { JwtInterceptor } from 'src/app/_role/jwt.interceptor';
import { ErrorInterceptor } from 'src/app/_role/error.interceptor';

@NgModule({
    declarations: [],
    imports: [
        CommonModule,
        HrRoutingModule,
        NzTableModule,
        // ApiModule.forRoot({ rootUrl: 'http://erp-hr.vm.devetek.com/api' }),
        ApiModule.forRoot({ rootUrl: 'http://erp-hr.vm.devetek.com/api' }),
        ApiModulePayroll.forRoot({ rootUrl: 'http://erp-payroll.vm.devetek.com/api' }),
        ApiModuleUser.forRoot({ rootUrl: 'http://erp-user.vm.devetek.com/api' }),
        ApiModuleDoc.forRoot({ rootUrl: 'http://erp-document.vm.devetek.com/api' }),


    ],
    providers: [
        { provide: HTTP_INTERCEPTORS, useClass: JwtInterceptor, multi: true },
        { provide: HTTP_INTERCEPTORS, useClass: ErrorInterceptor, multi: true },
    ],
})
export class HrModule { }
